import { Injectable } from "@angular/core";
import { CommonService } from "./common/common.service";
import { Observable } from "rxjs";
import { UrlService } from './url/url.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/internal/operators';
import { ApiService } from './api/api.service';

@Injectable({
  providedIn: "root"
})
export class CouponService {
  BASE_URL: any = this.url.SERVER_URL + '/api/';
  apiEndPoints:any;
  constructor(private comm: CommonService,
    private url:UrlService,
    private http: HttpClient,
    private api:ApiService) {
    {
      this.apiEndPoints = {
        //Coupon
        couponListing:'admin/couponListing',
        addProduct: 'admin/addProduct',
        editProduct:'admin/editProduct',
        deleteProduct:'admin/deleteProduct'
      }
      for (let key in this.apiEndPoints) {
        this.apiEndPoints[key] = this.BASE_URL + this.apiEndPoints[key];
      }
  }
}
protected getHeaders(): { headers: HttpHeaders } {
   return {
     headers: new HttpHeaders({
      'Accept': 'application/json',
       'Authorization': 'Bearer '+this.api.getToken()
     })
   };
 }
 couponListing(){
  return this.http
  .get<any>(this.apiEndPoints.couponListing,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('Coupon Listing'))
  );
}
addProduct(product: any): Observable<any> {
  const data:FormData=new FormData();
  data.append('name',product.name);
  data.append('image',product.image);
  data.append('description',product.description);
  if(product.status){
    data.append('show_in_website','1');
  }else{
    data.append('show_in_website','0');
  }
  data.append('price',product.prize);
  data.append('arabic_name',product.arabicName);
  data.append('arabic_description',product.arabicDescription);
   return this.http
   .post<any>(this.apiEndPoints.addProduct, data,this.getHeaders())
   .pipe(
     catchError(this.handleError<any>('Add Product'))
   );
 }
 editProduct(product: any): Observable<any> {
  const data:FormData=new FormData();
  data.append('id',product.id);
  data.append('name',product.name);
  data.append('image',product.image);
  data.append('description',product.description);
  if(product.status==true || product.status==1){
    data.append('show_in_website','1');
  }else{
    data.append('show_in_website','0');
  }
  data.append('price',product.prize);
   return this.http
   .post<any>(this.apiEndPoints.editProduct, data,this.getHeaders())
   .pipe(
     catchError(this.handleError<any>('Edit Product'))
   );
 }
 deleteProduct(id:any){
  const data:FormData=new FormData();
  data.append('id',id);
  return this.http
  .post<any>(this.apiEndPoints.deleteProduct,data,this.getHeaders())
  .pipe(
    catchError(this.handleError<any>('Delete Product'))
  );
 }
 private handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {
    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead
    return;
  };
}
}
